<template>
  <div id="search" ref="search">
    search
  </div>
</template>

<script>
import {search} from '@/libs/api'
export default {
  name: 'browser-search',
  mounted: function(){
    if(this.$store.getters.getSearchKeywords != '') {
      search({keywords: this.$store.getters.getSearchKeywords}).then((data)=>{
      this.$refs['search'].innerHTML+=JSON.stringify(data)
    })
    }
  }
}
</script>

<style lang="scss" scoped>
#search {
  overflow-y: overlay;
}
</style>